@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  color: rgba(0, 0, 0, 0.87);
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 0.81rem;
  font-weight: 400;
  line-height: 1.25rem;
  margin: 0;
  min-width: 18.75rem;
}

* {
  padding: 0;
  margin: 0;
}

.class__title {
  font-family: "Open Sans", Roboto, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.1px;
  line-height: 24px;
  flex-grow: 1;
  flex-shrink: 1;
  margin: 18px 24px 16px 24px;
  min-width: 0;
}
.class__content {
  padding-top: 0;
  margin-top: 0;
  font-family: "Roboto";
  margin-left: 10px;
}
.class__link {
  color: #4285f4;
  text-decoration: none;
  cursor: pointer;
  margin-right: 3px;
}
.class__text {
  /* display: flex; */
  margin-bottom: 10px;
}
.class__link2 {
  color: #4285f4;
  text-decoration: none;
  cursor: pointer;
  margin-left: 3px;
}

.class__checkboxWrapper {
  background-color: #f5f5f5;
  display: flex;
  margin-top: 1.5rem;
  overflow: hidden;
  padding: 1rem;
}
.class__checkboxWrapper > p {
  font-family: "Roboto";
  margin-left: 10px;
}
.form__input {
  margin-bottom: 10px !important;
  width: 70vw;
}
.form__inputs:last-of-type {
  margin-bottom: 40px;
}
.form__inputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.MuiFilledInput-root {
  background-color: rgba(0, 0, 0, 0.04) !important;
}

@media only screen and (min-width: 842px) {
  .form__input {
    width: 50vw !important;
  }
}

.joined {
  display: flex;
  flex-wrap: wrap;
  padding-left: 1.5rem;
  padding-top: 1.5rem;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
.joined__list {
  display: flex;
  flex-direction: column;
  height: 18.375rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  overflow: hidden;
  position: relative;
  text-align: left;
  width: 18.75rem;
  background-color: #fff;
  border: 0.0625rem solid #dadce0;
  border-radius: 0.5rem;
}
.joined__imgWrapper {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: #174ea6;
}
.joined__image {
  background-image: url(https://gstatic.com/classroom/themes/Physics.jpg);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.joined__container {
  position: relative;
}
.joined__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 4.5rem;
  padding: 1rem 1rem 0.75rem;
  position: relative;
}
.joined__title {
  color: white;
  font-family: "Roboto";
  text-decoration: none;
}
.joined__title h2 {
  font-weight: 400;
}
.joined__title h2:hover {
  text-decoration: underline;
}
.joined__owner {
  color: white;
  font-weight: 400 !important;
  font-size: 15px !important;
}
.joined__avatar {
  float: right;
  height: 4.6875rem !important;
  position: relative !important;
  margin-top: -3.09375rem !important;
  width: 4.6875rem !important;
  text-align: left;
}
.joined__bottom {
  margin-top: 40%;
  display: flex;
  color: #5f6368;
  justify-content: flex-end;
  border-top: 0.2px solid #e0e0e0;
  padding: 10px;
}
.joined__bottom > * {
  margin-right: 20px;
}

